/* Estilos del Contenedor Principal */
.credential-container {
  width: 400px;
  border: none;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.2);
  font-family: 'Arial', sans-serif;
  display: flex;
  flex-direction: column;
  margin: 20px auto;
  border-radius: 15px;
  overflow: hidden;
}

/* Estilos del Encabezado */
.header {
  background: linear-gradient(to bottom, #98fb98, #008000);
  padding: 20px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: 2px solid #006400;
}

.logo-container {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
  border: 3px solid #006400;
  overflow: hidden;
  background-color: #fff;
}

.logo {
  max-width: 90%;
  max-height: 90%;
  display: block;
}

.title {
  font-size: 1.5rem;
  color: #fff;
  margin-bottom: 5px;
  font-weight: bold;
}

.subtitle {
  font-size: 1.1rem;
  color: #fff;
  margin-bottom: 15px;
}

/* Estilos del Cuerpo */
.body {
  padding: 20px;
  background-color: #fff;
}

.body p {
  margin-bottom: 10px;
  font-size: 1rem;
  color: #000;
}

.body strong {
  color: #000;
  font-weight: bold;
}

.body ul {
  list-style: none;
  padding: 0;
  margin-bottom: 20px;
}

.body li {
  margin-bottom: 10px;
  font-size: 1rem;
  color: #000;
}

.body li::before {
  content: "•";
  color: #008000;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}

/* Estilos del Pie */
.footer {
  background-color: #f5f5f5;
  padding: 15px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  border-top: 2px solid #006400;
}

.footer p {
  font-size: 0.9rem;
  color: #000;
  margin: 0;
}

.line {
  height: 2px;
  background-color: #000;
  margin-top: 5px;
  margin-bottom: 10px;
}

.doctor-info {
  margin-bottom: 15px;
}

.doctor-info p {
  margin-bottom: 0px;
}

.qr-code {
  margin-top: 10px;
}

/* Progreso */
.progress {
  margin-top: 20px;
  font-size: 1rem;
  font-weight: bold;
  color: #006400;
}

.start-button {
  background-color: #4A90E2;
  color: white;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
  text-align: center;
}

.start-button:hover {
  background-color: #357ABD;
}

/* Estilos del Formulario */
.form-container {
  display: flex;
  flex-direction: column;
  width: 400px;
  margin: 20px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.2);
}

.input-group {
  margin-bottom: 15px;
}

.input-group label {
  display: block;
  margin-bottom: 5px;
}

.input-group input {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
}

.submit-button {
  background-color: #008000;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
}

.submit-button:hover {
  background-color: #006400;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}


/* Estilo del campo de código */
.code-input {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 10px;
  box-sizing: border-box;
}

/* Estilo para el mensaje de error */
.error-text {
  color: red;
  font-size: 0.9rem;
  margin-top: 5px;
}

/* Botón de avanzar */
.start-button {
  background-color: #4A90E2;
  color: white;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
  text-align: center;
}

.start-button:hover {
  background-color: #357ABD;
}
